import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';


@Injectable({
    providedIn: 'root'
})
export class ReviewService {

    constructor(public http: HttpClient, public storage: Storage) { 
    }
    
    
    public getReviews(userId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/reviews/" + userId + "?token=" + token).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
    
    public createReview(review){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/reviews?token=" + token, review).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }    
    
}
