import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';


@Injectable({
  providedIn: 'root'
})
export class ProfileService {

    constructor(public http: HttpClient, public storage: Storage) { }
    
    
    public getProfile(userId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/profiles/" + userId + "?token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
 
      
    public updateProfile(profile){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/profiles/" + profile.user_id + "?token=" + token, profile).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
    
}
